@charset "UTF-8";
.u-margin-bottom-lg {
  margin-bottom: 10rem; }

.Button {
  word-wrap: none;
  cursor: pointer;
  transition: all 0.5s; }
  .Button:hover {
    transform: translateY(-0.6rem);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15); }
  .Button:active {
    box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.15);
    transform: translateY(-0.3rem); }

.primary {
  z-index: 1;
  position: relative;
  cursor: pointer;
  text-align: center;
  padding: 1.5rem 4rem;
  font-size: 1.6rem;
  border-radius: 3rem;
  display: inline-block;
  text-transform: uppercase; }
  .primary::before {
    border-radius: inherit;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    position: absolute;
    display: block;
    background-color: inherit;
    width: 50%;
    height: 50%;
    content: " ";
    transition: all 0.5s; }
  .primary:hover::before {
    transform: translate(-50%, -50%) scale(3);
    opacity: 0; }

.primary--white {
  color: #777;
  background-color: #ffffff; }

.primary--purple {
  color: #ffffff;
  background-color: #786ce8; }

.button--after::after {
  content: " →";
  color: inherit; }

.secondary {
  text-transform: capitalize;
  font-weight: 500;
  text-align: center;
  padding: 0 0.3rem;
  padding-bottom: 0.1rem;
  display: inline-block;
  font-size: 1.5rem;
  color: #786ce8;
  border-bottom: 0.1rem solid #786ce8; }
  .secondary:hover {
    background-color: #786ce8;
    color: #ffffff; }
